'use client';

import { useState, useEffect, useCallback } from 'react';
import Link from 'next/link';
import AdminHeader from '@/components/AdminHeader';
import {
  fetchAllPools,
  formatCurrency,
  formatPercent,
  getPhaseIcon,
  getPhaseLabel,
  getPhaseColor,
  AllPoolsResponse,
  AgentPool,
} from '@/lib/pool-api';
import { API_BASE } from '@/lib/config';

export default function PoolsPage() {
  const [pools, setPools] = useState<AgentPool[]>([]);
  const [totals, setTotals] = useState({
    totalBalance: 0,
    totalPools: 0,
    byPhase: { retention: 0, normal: 0, release: 0 },
    volume24h: { bets: 0, wins: 0, ggr: 0 },
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Filters
  const [phaseFilter, setPhaseFilter] = useState<string>('all');
  const [sortBy, setSortBy] = useState<'balance' | 'name' | 'phase'>('balance');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');

  const loadPools = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetchAllPools();
      if (response.success && response.data) {
        setPools(response.data.pools || []);
        setTotals(response.data.totals || {
          totalBalance: 0,
          totalPools: 0,
          byPhase: { retention: 0, normal: 0, release: 0 },
          volume24h: { bets: 0, wins: 0, ggr: 0 },
        });
      }
    } catch (err) {
      // If admin endpoint doesn't exist, try to fetch agents and their pools
      try {
        const agentsRes = await fetch(`${API_BASE}/agent/agents`);
        const agentsData = await agentsRes.json();
        const agents = agentsData.data || agentsData || [];
        
        // Fetch pool for each agent
        const poolPromises = agents.map(async (agent: { id: string; name: string }) => {
          try {
            const poolRes = await fetch(`${API_BASE}/pool/${agent.id}`);
            const poolData = await poolRes.json();
            return {
              agentId: agent.id,
              agentName: agent.name,
              balance: poolData.data?.balance || 0,
              currentPhase: poolData.data?.currentPhase || 'normal',
              lastUpdated: poolData.data?.lastUpdated || new Date().toISOString(),
            };
          } catch {
            return {
              agentId: agent.id,
              agentName: agent.name,
              balance: 0,
              currentPhase: 'normal' as const,
              lastUpdated: new Date().toISOString(),
            };
          }
        });
        
        const poolsData = await Promise.all(poolPromises);
        setPools(poolsData);
        
        // Calculate totals
        const totalBalance = poolsData.reduce((sum, p) => sum + p.balance, 0);
        const byPhase = {
          retention: poolsData.filter(p => p.currentPhase === 'retention').length,
          normal: poolsData.filter(p => p.currentPhase === 'normal').length,
          release: poolsData.filter(p => p.currentPhase === 'release').length,
        };
        
        setTotals({
          totalBalance,
          totalPools: poolsData.length,
          byPhase,
          volume24h: { bets: 0, wins: 0, ggr: 0 },
        });
      } catch {
        setError('Falha ao carregar pools');
      }
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    loadPools();
    // Auto-refresh every 30 seconds
    const interval = setInterval(loadPools, 30000);
    return () => clearInterval(interval);
  }, [loadPools]);

  // Filter and sort pools
  const filteredPools = pools
    .filter(pool => phaseFilter === 'all' || pool.currentPhase === phaseFilter)
    .sort((a, b) => {
      let comparison = 0;
      switch (sortBy) {
        case 'balance':
          comparison = a.balance - b.balance;
          break;
        case 'name':
          comparison = a.agentName.localeCompare(b.agentName);
          break;
        case 'phase':
          comparison = a.currentPhase.localeCompare(b.currentPhase);
          break;
      }
      return sortOrder === 'asc' ? comparison : -comparison;
    });

  return (
    <div className="min-h-screen bg-linear-to-br from-slate-900 via-slate-800 to-slate-900">
      <AdminHeader />

      <main className="mx-auto max-w-7xl px-6 py-8">
        {/* Header */}
        <div className="mb-6 flex items-center justify-between">
          <div>
            <h2 className="text-2xl font-semibold text-white flex items-center gap-3">
              <span>üè¶</span> Pools dos Agentes
            </h2>
            <p className="text-slate-400 text-sm mt-1">
              Vis√£o geral de todos os pools do sistema
            </p>
          </div>
          <button
            onClick={loadPools}
            disabled={loading}
            className="flex items-center gap-2 rounded-lg bg-emerald-600 px-4 py-2 text-white hover:bg-emerald-700 transition disabled:opacity-50"
          >
            {loading ? (
              <div className="h-4 w-4 animate-spin rounded-full border-2 border-white border-t-transparent" />
            ) : (
              <span>üîÑ</span>
            )}
            Atualizar
          </button>
        </div>

        {error && (
          <div className="mb-6 rounded-lg bg-red-500/20 border border-red-500/50 p-4 text-red-300 flex items-center gap-2">
            <span>‚ö†Ô∏è</span> {error}
          </div>
        )}

        {/* Totals Cards */}
        <div className="mb-6 grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
          <div className="rounded-xl border border-emerald-500/30 bg-linear-to-br from-emerald-500/20 to-emerald-600/10 p-5">
            <div className="text-2xl mb-2">üí∞</div>
            <p className="text-sm text-slate-400">Saldo Total</p>
            <p className="text-2xl font-bold text-emerald-400">
              {loading ? '...' : formatCurrency(totals.totalBalance)}
            </p>
          </div>
          
          <div className="rounded-xl border border-blue-500/30 bg-linear-to-br from-blue-500/20 to-blue-600/10 p-5">
            <div className="text-2xl mb-2">üìä</div>
            <p className="text-sm text-slate-400">Pools Ativos</p>
            <p className="text-2xl font-bold text-blue-400">
              {loading ? '...' : totals.totalPools}
            </p>
          </div>
          
          <div className="rounded-xl border border-amber-500/30 bg-linear-to-br from-amber-500/20 to-amber-600/10 p-5">
            <div className="text-2xl mb-2">üìà</div>
            <p className="text-sm text-slate-400">Volume 24h</p>
            <p className="text-2xl font-bold text-amber-400">
              {loading ? '...' : formatCurrency(totals.volume24h.bets)}
            </p>
          </div>
          
          <div className="rounded-xl border border-purple-500/30 bg-linear-to-br from-purple-500/20 to-purple-600/10 p-5">
            <div className="text-2xl mb-2">üéØ</div>
            <p className="text-sm text-slate-400">GGR 24h</p>
            <p className="text-2xl font-bold text-purple-400">
              {loading ? '...' : formatCurrency(totals.volume24h.ggr)}
            </p>
          </div>
        </div>

        {/* Phase Distribution */}
        <div className="mb-6 rounded-xl border border-slate-700 bg-slate-800/50 p-5">
          <h3 className="text-lg font-medium text-white mb-4">Distribui√ß√£o por Fase</h3>
          <div className="grid grid-cols-3 gap-4">
            <div className="flex items-center gap-3 p-4 rounded-lg bg-red-500/10 border border-red-500/20">
              <span className="text-3xl">üî¥</span>
              <div>
                <p className="text-sm text-slate-400">Reten√ß√£o</p>
                <p className="text-2xl font-bold text-red-400">{totals.byPhase.retention}</p>
              </div>
            </div>
            <div className="flex items-center gap-3 p-4 rounded-lg bg-amber-500/10 border border-amber-500/20">
              <span className="text-3xl">üü°</span>
              <div>
                <p className="text-sm text-slate-400">Normal</p>
                <p className="text-2xl font-bold text-amber-400">{totals.byPhase.normal}</p>
              </div>
            </div>
            <div className="flex items-center gap-3 p-4 rounded-lg bg-emerald-500/10 border border-emerald-500/20">
              <span className="text-3xl">üü¢</span>
              <div>
                <p className="text-sm text-slate-400">Libera√ß√£o</p>
                <p className="text-2xl font-bold text-emerald-400">{totals.byPhase.release}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Filters */}
        <div className="mb-4 flex flex-wrap gap-4 items-center">
          <div className="flex items-center gap-2">
            <label className="text-sm text-slate-400">Fase:</label>
            <select
              value={phaseFilter}
              onChange={(e) => setPhaseFilter(e.target.value)}
              className="rounded-lg bg-slate-700 border border-slate-600 px-3 py-2 text-white text-sm"
            >
              <option value="all">Todas</option>
              <option value="retention">üî¥ Reten√ß√£o</option>
              <option value="normal">üü° Normal</option>
              <option value="release">üü¢ Libera√ß√£o</option>
            </select>
          </div>
          
          <div className="flex items-center gap-2">
            <label className="text-sm text-slate-400">Ordenar:</label>
            <select
              value={sortBy}
              onChange={(e) => setSortBy(e.target.value as 'balance' | 'name' | 'phase')}
              className="rounded-lg bg-slate-700 border border-slate-600 px-3 py-2 text-white text-sm"
            >
              <option value="balance">Saldo</option>
              <option value="name">Nome</option>
              <option value="phase">Fase</option>
            </select>
            <button
              onClick={() => setSortOrder(prev => prev === 'asc' ? 'desc' : 'asc')}
              className="px-3 py-2 rounded-lg bg-slate-700 border border-slate-600 text-white text-sm hover:bg-slate-600"
            >
              {sortOrder === 'asc' ? '‚Üë' : '‚Üì'}
            </button>
          </div>
          
          <div className="ml-auto text-sm text-slate-400">
            {filteredPools.length} de {pools.length} pools
          </div>
        </div>

        {/* Pools Table */}
        <div className="rounded-xl border border-slate-700 bg-slate-800/50 overflow-hidden">
          {loading ? (
            <div className="flex items-center justify-center py-20">
              <div className="h-10 w-10 animate-spin rounded-full border-4 border-emerald-500 border-t-transparent" />
            </div>
          ) : filteredPools.length === 0 ? (
            <div className="text-center py-20 text-slate-400">
              <span className="text-5xl block mb-4">üè¶</span>
              <p className="text-lg">Nenhum pool encontrado</p>
              <p className="text-sm mt-2">Os pools ser√£o criados automaticamente para cada agente</p>
            </div>
          ) : (
            <table className="w-full">
              <thead className="bg-slate-700/50">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-medium text-slate-300">Agente</th>
                  <th className="px-6 py-4 text-right text-sm font-medium text-slate-300">Saldo</th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-slate-300">Fase</th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-slate-300">Atualizado</th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-slate-300">A√ß√µes</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-slate-700/50">
                {filteredPools.map((pool) => (
                  <tr key={pool.agentId} className="hover:bg-slate-700/30 transition">
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-3">
                        <div className="h-10 w-10 rounded-full bg-linear-to-br from-emerald-500 to-blue-500 flex items-center justify-center text-white font-bold">
                          {pool.agentName?.charAt(0)?.toUpperCase() || '?'}
                        </div>
                        <div>
                          <p className="font-medium text-white">{pool.agentName || 'Sem nome'}</p>
                          <p className="text-xs text-slate-500 font-mono">{pool.agentId.slice(0, 8)}...</p>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-right">
                      <span className={`font-bold ${pool.balance > 0 ? 'text-emerald-400' : 'text-red-400'}`}>
                        {formatCurrency(pool.balance)}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-center">
                      <span className={`inline-flex items-center gap-2 px-3 py-1 rounded-full text-sm border ${getPhaseColor(pool.currentPhase)}`}>
                        {getPhaseIcon(pool.currentPhase)} {getPhaseLabel(pool.currentPhase).replace(/^[^\s]+\s/, '')}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-center text-sm text-slate-400">
                      {new Date(pool.lastUpdated).toLocaleTimeString('pt-BR')}
                    </td>
                    <td className="px-6 py-4 text-center">
                      <Link
                        href={`/pools/${pool.agentId}`}
                        className="inline-flex items-center gap-1 px-3 py-1.5 rounded-lg bg-blue-600/20 text-blue-400 border border-blue-600/30 hover:bg-blue-600/30 transition text-sm"
                      >
                        üëÅÔ∏è Ver Detalhes
                      </Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </main>
    </div>
  );
}
